<template>
  <div class="home">
    <!-- Movies  -->

    <div
      class="movies"
      v-if="
        newMoviesData && moviesNotFonudError != true && netWorkError != true
      "
    >
      <div class="category" v-if="genre">
        <h1>
          {{ searchText }} : <span> {{ genre }} </span>
        </h1>
      </div>
      <div class="category" v-else>
        <h1>category : <span>animated</span></h1>
      </div>

      <div class="movies-list">
        <router-link
          class="movie"
          v-for="movieData in newMoviesData"
          :key="movieData.imdbID"
          :to="{ name: 'MovieDetails', params: { id: movieData.imdbID } }"
          :imdbID="movieData.imdbID"
        >
          <img
            v-if="movieData.Poster != 'N/A'"
            class="feature-img"
            :src="movieData.Poster"
            :alt="movieData.Title"
          />
          <img src="../assets/images/DHbaSYYv_400x400.jpg" v-else alt="" />
          <h3 class="movie-title">{{ movieData.Title }}</h3>
          <p class="release-date">release date : {{ movieData.Year }}</p>
        </router-link>
      </div>
    </div>
    <!-- Movies -->
    <Loading v-if="newMoviesData == null" />
    <!-- MoviesNotFound  -->
    <MoviesNotfound v-if="moviesNotFonudError === true" />
    <!-- MoviesNotFound  -->

    <!-- NetWorkError -->
    <NetWorkError v-if="netWorkError === true" />
    <!-- NetWorkError -->

    <!-- Next n Prev -->
    <div
      class="next-n-prev"
      v-if="
        newMoviesData && moviesNotFonudError != true && netWorkError != true
      "
    >
      <button @click="pageHandler" class="prev-btn">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </button>
      <button @click="pageHandler" class="next-btn">
        <i class="fa fa-angle-right" aria-hidden="true"></i>
      </button>
    </div>
    <!-- Next n Prev -->
  </div>
</template>

<script>
import { watch, ref } from "vue";
import getData from "../composable/getData";
import MoviesNotfound from "../component/MoviesNotFound";
import NetWorkError from "../component/NewWorkError";
import Loading from "../component/Loading";
export default {
  props: ["genre", "searchText"],
  components: { MoviesNotfound, NetWorkError, Loading },

  setup(props) {
    document.title = "Home"
    const { moviesData } = getData(`s=${props.genre}&page=1`);
    const newMoviesData = ref(null);
    const moviesNotFonudError = ref(false);
    const netWorkError = ref(false);
    // Watch moviesData
    watch(moviesData, () => {
      newMoviesData.value = null;
      newMoviesData.value = moviesData._rawValue;
      if (moviesData._rawValue.NetWorkError) {
        netWorkError.value = true;
        console.log("net out");
        console.log(moviesData);
      } else if (moviesData._rawValue.NotFoundError) {
        if (netWorkError.value != true) {
          moviesNotFonudError.value = true;
          console.log("dadw");
        }
      }
    });
    // updateOnlineStatus
    function updateOnlineStatus() {
      const condition = navigator.onLine;
      if (condition === true) {
        console.log("True", condition);
        netWorkError.value = false;
        const { moviesData } = getData(`s=${props.genre}&page=1`);
        watch(moviesData, () => {
          newMoviesData.value = moviesData._rawValue;
          if (moviesData._rawValue.NetWorkError) {
            netWorkError.value = true;
            console.log("net out");
            console.log(moviesData);
          }
        });
      } else {
        console.log("False", condition);
        netWorkError.value = true;
      }
    }
    window.addEventListener("online", updateOnlineStatus);
    window.addEventListener("offline", updateOnlineStatus);
    // Wacth props.genre
    watch(
      () => props.genre,
      () => {
        count = 1;
        moviesNotFonudError.value = false;
        netWorkError.value = false;
        newMoviesData.value = null;
        const { moviesData } = getData(`s=${props.genre}`);
        console.log("this home", moviesData);
        watch(moviesData, () => {
          console.log("dsds", moviesData);
          newMoviesData.value = moviesData._rawValue;
          if (moviesData._rawValue.NotFoundError) {
            moviesNotFonudError.value = true;
          }
          if (moviesData._rawValue.NetWorkError) {
            netWorkError.value = true;
          }
        });
      }
    );
    // pageHandler
    let count = 1;
    const pageHandler = (e) => {
      if (e.target.className != "prev-btn") {
        count++;
        const { moviesData } = getData(`s=${props.genre}&page=${count}`);
        newMoviesData.value = null;
        moviesNotFonudError.value = false;
        netWorkError.value = false;
        watch(moviesData, () => {
          console.log("next", moviesData);
          newMoviesData.value = moviesData._rawValue;
          if (moviesData._rawValue.NetWorkError) {
            netWorkError.value = true;
          } else if (moviesData._rawValue.Error) {
            moviesNotFonudError.value = true;
          } else if (moviesData._rawValue.NotFoundError) {
            moviesNotFonudError.value = true;
            console.log(323322);
          }
        });
      } else {
        count--;
        if (count <= 0) count = 1;
        const { moviesData } = getData(`s=${props.genre}&page=${count}`);
        moviesNotFonudError.value = false;
        netWorkError.value = false;
        newMoviesData.value = null;
        watch(moviesData, () => {
          newMoviesData.value = moviesData._rawValue;
          if (moviesData._rawValue.NetWorkError) {
            netWorkError.value = true;
          } else if (moviesData._rawValue.Error) {
            moviesNotFonudError.value = true;
          }
        });
      }
    };
    return {
      newMoviesData,
      pageHandler,
      moviesNotFonudError,
      netWorkError,
    };
  },
};
</script>
