<template>
  <!-- MoviesDetails -->
  <transition
    :duration="750"
    enter-active-class="animate__animated animate__zoomIn"
    leave-active-class="animate__animated animate__zoomIn"
  >
    <div v-if="data" class="movie-details">
      <ul v-if="!data._rawValue.NewWorkError">
        <li>
          <h1>{{ data._rawValue.Title }}</h1>
          ( {{ data._rawValue.Released }} )
        </li>
        <li>
          <i class="fa fa-star" aria-hidden="true"></i>
          {{ data._rawValue.imdbRating }}
          <span>{{ data._rawValue.imdbVotes }}</span>
        </li>
      </ul>
      <div class="movie-info" v-if="!data._rawValue.NewWorkError">
        <div v-if="data._rawValue.Poster != 'N/A'" class="poster">
          <img :src="data._rawValue.Poster" alt="" />
        </div>
        <div v-else class="poster">
          <img src="../assets/images/DHbaSYYv_400x400.jpg" alt="" />
        </div>
        <div class="plot">
          <p>{{ data._rawValue.Plot }}</p>
          <div class="more-info">
            <p>
              {{ data._rawValue.Rated }} | {{ data._rawValue.Runtime }} |
              {{ data._rawValue.Genre }} | {{ data._rawValue.Released }} |
              {{ data._rawValue.Type }} | Seasons :
              {{ data._rawValue.totalSeasons }}
            </p>
            <ul>
              <li>
                Director: <span>{{ data._rawValue.Director }}</span>
              </li>
              <li>
                Writers: <span> {{ data._rawValue.Writer }}</span>
              </li>
              <li>
                Stars: <span>{{ data._rawValue.Actors }}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </transition>
  <Loading v-if="data == null" />
  <!-- MoviesDetails -->

  <!-- MoviesNotFound -->
  <MoviesNotfound v-if="moviesNotFonudError === true" />
  <!-- MoviesNotFound -->

  <!-- NetWorkError -->
  <NetWorkError v-if="netWorkError === true" />
  <!-- NetWorkError -->
</template>
<script>
import getData from "../composable/getData";
import MoviesNotfound from "../component/MoviesNotFound";
import NetWorkError from "../component/NewWorkError";
import Loading from "../component/Loading";
import { ref, watch } from "vue";
import { useRoute } from "vue-router";
export default {
  props: ["genre", "searchText"],
  components: { MoviesNotfound, NetWorkError, Loading },
  data() {
    return {
      id: this.$route.params.id,
      genreValue: this.genre,
    };
  },
  setup() {
    document.title = "MoviesDetails"
    const route = useRoute();
    console.log(route.params.id);
    const { moviesData } = getData(`i=${route.params.id}&plot=full`);
    console.log(moviesData);
    const data = ref(null);
    const moviesNotFonudError = ref(false);
    const netWorkError = ref(false);
    console.log(moviesData._rawValue);
    // Watch moviesData
    watch(moviesData, () => {
      console.log("moviesList", moviesData);
      if (moviesData._rawValue.Error) {
        moviesNotFonudError.value = true;
      } else if (moviesData._rawValue.NetWorkError) {
        netWorkError.value = true;
        console.log(12345);
        return;
      } else if (moviesData._rawValue.NotFoundError) {
        if (netWorkError.value != true) {
          moviesNotFonudError.value = true;
          console.log(54321);
        }
      } else {
        data.value = moviesData;
        console.log("this si", data.value._rawValue);
        console.log(1);
      }
    });

    return { route, data, moviesNotFonudError, netWorkError };
  },
};
</script>

<style lang="scss">
