    align-items: center;
    padding: 30px 0px;
    li {
      font-size: 1.2rem;
      .h1 {
        padding: 10px 0px;
      }
      span {
        display: block;
      }
      i {
        color: #42b883;
      }
    }
  }
  .movie-info {
