        }
        ul {
          width: 100%;
          display: block;
          padding: 10px 0px;
          li {
            padding: 10px 0px;
            display: flex;
            span {
              display: inline-block;
              color: #42b883;
              padding: 0px 10px;
              line-height: 25px;
            }
          }
        }
      }
    }
  }
}
