<template>
  <!-- Header Start from here -->
  <header>
    <div class="top-bar">
      <div class="logo">
        <div @click="toHomePage">
          <h1>Gets<span>Movies</span></h1>
        </div>
      </div>
      <div class="search">
        <input class="input-search" type="text" placeholder="search" />
        <button class="search-btn" @click="searchMovies">search</button>
      </div>
    </div>
    <div class="genre" v-if="genre">
      <ul>
        <li @click="genreHandler">action</li>
        <li @click="genreHandler">horror</li>
        <li @click="genreHandler">animated</li>
        <li @click="genreHandler">comedy</li>
        <li @click="genreHandler">adventure</li>
        <li @click="genreHandler">sci-fi</li>
        <li @click="genreHandler">drama</li>
        <li @click="genreHandler">CRIME</li>
        <li @click="genreHandler">MYSTERY</li>
        <li @click="genreHandler">THRILLER</li>
      </ul>
    </div>
  </header>
  <!-- Header ended here -->

  <!-- Main Start from here -->
  <main>
    <router-view :genre="genre" :searchText="searchText" />
  </main>
  <!-- Main ended here -->

  <!-- Footer Start from here -->
  <footer>
    <p><span>gets</span>Movies Â© 2021</p>
  </footer>
  <!-- Footer ended here -->
</template>
<script>
import { ref } from "vue";
import { useRouter } from "vue-router";

export default {
  name:"home",
  setup() {
    const categoryArray = [
      "Animated",
      "Horror",
      "Action",
      "Comedy",
      "Adventure",
      "Drama",
      "CRIME",
      "MYSTERY",
      "THRILLER",
    ];
    const router = useRouter();
    const randomSearch = Math.floor(Math.random() * 8) + 1;
    const genre = ref(categoryArray[randomSearch]);
    const searchText = ref("Category");
    // toHomePage
    const toHomePage = () => {
      router.push({
        name: "Home",
      });
      genre.value = categoryArray[randomSearch];
      searchText.value = "Category";
    };
    // genreHandler
    const genreHandler = (e) => {
      router.push({
        name: "Home",
      });
      searchText.value = "Category";
      genre.value = e.target.textContent;
    };
    // searchMovies
    const searchMovies = () => {
      router.push({
        name: "Home",
      });
      searchText.value = "Search for";
      genre.value = document.querySelector(".input-search").value;
    };
    return {
      router,
      genre,
      toHomePage,
      searchText,
      randomSearch,
      genreHandler,
      categoryArray,
      searchMovies,
    };
  },
};
</script>
>

